body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #303030;
  background-image: url("background.png");
  color: #FFFFFF;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Oxygen',
    monospace;
}

.mint-container{
  border-style: solid;
  border-width: 6px;
  border-color: pink;
  border-radius: 60px 60px 60px 60px;
  width: 550px;
  text-align: center;
  margin: auto;
  padding-top: 3%;
  padding-bottom: 5%;
  font-size: 1.3em;
  color: black;
font-weight: bolder
}